h2 {
  font-size: 2rem;
  color: #404145;
  font-family: Macan-Bold;
}

.trustedBy {
  justify-content: center;
  justify-items: center;
  // height: rem(95px);
  background-color: $clr17;
  .trustedByImg {
    display: flex;
    padding: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  h3 {
    font-family: Macan-Bold;
    display: flex;
    align-self: center;
    // padding: 26px;
    padding-left: 0;
    margin: 15px;
    margin-left: 0;
    color: #b5b6ba;
    line-height: normal;
    font-size: rem(16px);
  }

  ul {
    min-width: 664px;
  }
}

.selling-ad {
  background-color: #f1fdf7;

  .ad-grid {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr 1fr;

    gap: 190px;
    .ad-text {
      font-family: Macan-Regular;
      margin-bottom: 96px;
      margin-bottom: 1em;
      padding-top: em(62px);
      color: $clr1;
      line-height: em(24px);
      font-size: em(18px);
      font-weight: 450;
      margin-bottom: 96px;

      h2 {
        line-height: 1.25;
        margin-bottom: 0;
      }
      h4 {
        margin-bottom: 8px;
      }
      svg {
        height: 24px;
        width: 24px;
        margin-right: 10px;
        fill: #62646a;
        margin-top: 14px;
        opacity: 80%;
      }

      p {
        margin: 0;
        color: $clr4;
      }
    }
  }

  img {
    margin-top: em(140px);
    justify-self: center;
    align-self: center;
    margin-left: 15px;
  }
}
